.loader {
  width: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin-top: 10px;
  animation: rotation 1s linear infinite;
  display: block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.error {
